      clearInterval(interval);
      elements.simulation!.style.transitionDuration = '0s';
      state.value = 'finished';
    }
    i++;
  }, 1000 / fps)
  elements.simulation!.style.transitionDuration = totalTimeInSeconds + 's';
  elements.simulation!.classList.add(Math.random() > 0.5 ? 'stateA' : 'stateB');
}

function prepateInSuperposition() {
  elements.simulation!.classList.add('superposition');
  state.value = 'prepared'
}

onMounted(() => {
  for (let key in elements) {
    elements[key] = document.querySelector('#' + key);
  }
  console.log('myel', elements);
  for (let type of ['warmth', 'light', 'airParticle']) {
    const el = elements[type]!;
    centerSvgEl(elements.simulation!, el); // THEN put centering in the back of it
    el.style.display = 'none';
  }
  elements.simulation!.classList.add(object.value);
  watchEffect(() => {
    console.log('object value changed!')
    resetAnimationState();
    elements.simulation!.classList.remove(...elements.simulation!.classList);
    elements.simulation!.classList.add(object.value);
    state.value = 'object'
  })
})


